// I think for the background image slider, we should put the images inside div's
// in the HTML, and position text and button absolutely (over top the slider)
// within the Header component


.header {
  height: 600px;
  color: white;
  background-color: grey;
  // background-image: url(../../public/images/wave-white.png); // url for a background image: url("../../public/images/header-slider-img-01.jpg");
  // background-repeat: no-repeat;
  // background-position: bottom left;
  // background-size: 200%;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;

  @media only screen and (min-width: $tablet) {
    background-size: 100%;
    position: relative;
  }

  .swiper {
    height: 100%;
  }

  .swiper-slide {
    height: 100%;
    img{
      height: 100%;
    }
  }

  .swiper-pagination {
    bottom: 110px;
    @media only screen and (min-width: $tablet) {
      bottom: 140px;
    }
  }
  

  .links{
    display: none;
    @media only screen and (min-width: $tablet) {
      display: flex;
      justify-content: space-between;
      width: 100%;
      position: absolute;
      top: 0px;
      z-index: 5;
    }
  }

  .primary-links {
    font-family: 'Roboto Condensed';
    justify-content: space-between;
    display: flex;
    padding-left: 40px;
    margin-top: 10px;
    
    a {
      color: white;
    }

    @media only screen and (min-width: $tablet) {
      width: 275px;
      font-size: .9em;
    }

    @media only screen and (min-width: $desktop) {
      width: 320px;
    }

  }

  .secondary-links {
    font-family: 'Roboto Condensed';
    display: flex;
    justify-content: space-between;
    padding-right: 40px;

    a {
      color: white;
    }

    @media only screen and (min-width: $tablet) {
      width: 275px;
      font-size: .9em;
    }

    @media only screen and (min-width: $desktop) {
      width: 320px;
    }

  }

  button {
    width:13em;
    padding: 10px;
    margin:25px 25px 10px;
    align-content: center;
    text-transform: uppercase;
    background-color: rgba(0, 0, 0, 0);
    color: $primaryText;
    border: 1px solid $primaryText;
    cursor: pointer;
  }

  .text-container{
    position: absolute;
    top: 100px;
    z-index: 1;
    width:80%;
    bottom: 40px;

    @media only screen and (min-width: $tablet) {
      top: 250px;
    }
  }
  
  .header-overlay {
    // height: 80%;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    
    h2 {
      text-transform: uppercase;
      font-size: 1.6em;
      
    }
    .btn-container {
      
      @media only screen and (min-width: $tablet) {
        max-width: 400px;
        flex-direction: row;
        justify-content: center;
      }

      @media only screen and (min-width: $desktop) {
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      a:visited {
        color: white;
      }
    
      button {
        
        @media only screen and (min-width: $tablet) {
          margin-right: 15px;
        }

        @media only screen and (min-width: $desktop) {
          width:13em;
          padding: 10px;
          margin:25px 0 10px;
          align-content: center;
          text-transform: uppercase;
          background-color: rgba(0, 0, 0, 0);
          color: $primaryText;
          border: 1px solid $primaryText;
          cursor: pointer;
        }
      }
    }  
  }
}
